<template>
  <el-container class="home-container">
    <!-- 头部 -->
    <el-header class="header">
      <el-row>
        <el-col :span="8">
          <span class="system-title">进销存管理系统</span>
        </el-col>
        <el-col :span="8">&nbsp;</el-col>
        <el-col :span="8" class="header-right">
          <el-button type="primary" icon="el-icon-setting" size="small" class="header-btn">修改密码</el-button>
          <el-button type="danger" icon="el-icon-remove" size="small" class="header-btn" @click="outlogin">退出登录</el-button>
          <el-button type="info" icon="el-icon-user-solid" size="small" class="header-btn">{{ name }}</el-button>
        </el-col>
      </el-row>
    </el-header>

    <el-container>
      <!-- 侧边栏 -->
      <el-aside width="260px" class="sidebar">
        <el-menu router :default-openeds="['1', '2', '3', '4', '5']">
          <!-- 系统管理 -->
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-setting"></i>
              <span>系统管理</span>
            </template>
            <el-menu-item index="/index/user">
              <i class="el-icon-user"></i>
              <span>用户管理</span>
            </el-menu-item>
            <el-menu-item index="/index/role">
              <i class="el-icon-lock"></i>
              <span>权限管理</span>
            </el-menu-item>
          </el-submenu>

          <!-- 基础信息管理 -->
          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-document"></i>
              <span>基础信息管理</span>
            </template>
            <el-menu-item index="/index/supplier">
              <i class="el-icon-s-shop"></i>
              <span>供货商管理</span>
            </el-menu-item>
            <el-menu-item index="/index/warehouse">
              <i class="el-icon-bank-card"></i>
              <span>仓库管理</span>
            </el-menu-item>
          </el-submenu>

          <!-- 商品管理 -->
          <el-submenu index="3">
            <template slot="title">
              <i class="el-icon-box"></i>
              <span>商品管理</span>
            </template>
            <el-menu-item index="/index/inventory">
              <i class="el-icon-s-order"></i>
              <span>库存管理</span>
            </el-menu-item>
            <el-menu-item index="/index/inbound">
              <i class="el-icon-upload"></i>
              <span>商品入库</span>
            </el-menu-item>
            <el-menu-item index="/index/outbound">
              <i class="el-icon-download"></i>
              <span>商品出库</span>
            </el-menu-item>
            <el-menu-item index="/index/sales">
              <i class="el-icon-sell"></i>
              <span>商品销售</span>
            </el-menu-item>
          </el-submenu>

          <!-- 统计与报表 -->
          <el-submenu index="4">
            <template slot="title">
              <i class="el-icon-data-analysis"></i>
              <span>统计与报表</span>
            </template>
            <el-menu-item index="/index/monthly">
              <i class="el-icon-date"></i>
              <span>月度统计</span>
            </el-menu-item>
            <el-menu-item index="/index/yearly">
              <i class="el-icon-s-data"></i>
              <span>年度统计</span>
            </el-menu-item>
          </el-submenu>

          <!-- 财务管理 -->
          <el-submenu index="5">
            <template slot="title">
              <i class="el-icon-wallet"></i>
              <span>财务管理</span>
            </template>
            <el-menu-item index="/index/records">
              <i class="el-icon-tickets"></i>
              <span>收支记录</span>
            </el-menu-item>
            <el-menu-item index="/index/profit">
              <i class="el-icon-money"></i>
              <span>利润计算</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>

      <!-- 内容区域 -->
      <el-main class="main-content">
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "Index",
  methods: {
    outlogin() {
      this.$router.push("/login");
    },
  },
  mounted() {
    this.name = localStorage.getItem("name");
  },
  data() {
    return {
      name: "" // 当前登录用户
    };
  }
};
</script>

<style scoped>
/* 整体布局 */
.home-container {
  height: 100vh;
}

/* 头部样式 */
.header {
  background: #2c3e50;
  color: white;
  display: flex;
  align-items: center;
  padding: 10px 20px;
}

.system-title {
  font-size: 20px;
  font-weight: bold;
}

.header-right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 10px;
}

.header-btn {
  margin-left: 10px;
}

/* 侧边栏样式 */
.sidebar {
  background: #ffffff;
  height: 100vh;
  border-right: 1px solid #dcdcdc;
}

/* 主要内容区 */
.main-content {
  padding: 20px;
  background: #f5f5f5;
}
</style>



